.counter-preview {
  background-color: #2a2d34;
  display: flex;
  flex-direction: column; }
  @media screen and (min-width: 768px) {
    .counter-preview {
      flex-direction: row; } }
  .counter-preview .counter {
    background-color: #ffffff;
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center; }
    @media screen and (min-width: 768px) {
      .counter-preview .counter {
        width: 33%; } }
    .counter-preview .counter__btns {
      display: flex;
      align-items: center;
      justify-content: space-around;
      margin-bottom: 1rem; }
    .counter-preview .counter p {
      font-size: 2rem; }
  .counter-preview code,
  .counter-preview pre {
    background-color: #2a2d34;
    font-size: 0.6rem;
    width: 100%; }
    @media screen and (min-width: 768px) {
      .counter-preview code,
      .counter-preview pre {
        width: 67%;
        font-size: 1.2rem; } }
  .counter-preview .language {
    overflow: scroll;
    color: #fefefe; }
    @media screen and (min-width: 768px) {
      .counter-preview .language {
        padding: 0 0.5rem; } }
    .counter-preview .language .el {
      color: #d44950; }
    .counter-preview .language .attr {
      color: #4f9fcf; }
      .counter-preview .language .attr-val {
        color: #fdc45a; }
    .counter-preview .language .punc {
      color: #00ca00; }
