.tags {
  display: flex;
  margin-top: 1rem;
  flex-direction: column; }
  @media screen and (min-width: 768px) {
    .tags {
      flex-direction: row; } }
  .tags__tag {
    padding: 0.75rem;
    flex: 1;
    margin: 0 1rem; }
  .tags__headline {
    font-size: 1.2rem;
    text-decoration: none;
    color: #000000;
    font-weight: bold;
    position: relative; }
    .tags__headline:hover::after, .tags__headline:focus::after {
      content: "#";
      position: absolute;
      left: -0.65rem;
      top: 0;
      font-size: 1rem; }
  .tags__words {
    display: flex;
    flex-direction: column; }
  .tags .images {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 0.2em; }
    .tags .images > a {
      flex: 1; }
    .tags .images img {
      width: 50%; }
