.chart-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 100%; }

.chart {
  display: flex;
  flex-direction: column; }
  .chart-section {
    display: flex;
    flex-direction: column;
    align-items: center; }

.graph {
  width: 300px;
  height: 300px;
  border: 1px solid black;
  background: white;
  position: relative;
  border-top: 0;
  border-right: 0;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-end;
  margin-bottom: 7rem; }
  .graph .y-axis {
    position: absolute;
    left: -17%;
    transform: rotate(-90deg);
    top: 50%; }

.bar {
  border-bottom: 0;
  background-color: #a7aeb4;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  font-size: 0.9em;
  position: relative;
  flex: 0.1; }
  .bar p {
    margin: 0; }
  .bar:hover::before, .bar:focus::before {
    content: attr(data-count);
    position: absolute;
    top: -1.1rem; }

.library-title {
  transform: rotate(-45deg) translateX(-2em);
  transform-origin: right;
  position: absolute; }
