.disabled {
  pointer-events: none;
  cursor: not-allowed;
  opacity: 0.5; }
  .disabled:hover, .disabled:focus {
    cursor: not-allowed; }

body {
  background-color: #ffffff;
  letter-spacing: 1px; }

h2 {
  text-align: center; }

.container {
  padding: 0 2vw;
  margin: 0 auto;
  max-width: 1120px; }
  @media screen and (min-width: 768px) {
    .container {
      padding: 0 10vw; } }

p.section-description {
  padding: 0 1rem;
  margin: 0;
  margin-bottom: 0.5rem;
  text-align: center; }

a {
  color: #4fc0ec; }

.dark-text {
  color: #2a2d34; }

@keyframes color_change {
  0% {
    color: blue; }
  25% {
    color: orange; }
  50% {
    color: #ceca01; }
  75% {
    color: #14b614; }
  100% {
    color: red; } }

@keyframes bg_color_change {
  0% {
    background-color: blue; }
  25% {
    background-color: orange; }
  50% {
    background-color: #ceca01; }
  75% {
    background-color: #14b614; }
  100% {
    background-color: red; } }

.section {
  padding-top: 10vh;
  padding-bottom: 10vh; }
  .section--green, .section--shadow {
    background-color: #00ca00;
    box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.4); }

.colorize {
  -webkit-animation: color_change 7s infinite alternate;
  -moz-animation: color_change 7s infinite alternate;
  -ms-animation: color_change 7s infinite alternate;
  -o-animation: color_change 7s infinite alternate;
  animation: color_change 7s infinite alternate; }

.bg-colorize {
  -webkit-animation: bg_color_change 10s infinite alternate;
  -moz-animation: bg_color_change 10s infinite alternate;
  -ms-animation: bg_color_change 10s infinite alternate;
  -o-animation: bg_color_change 10s infinite alternate;
  animation: bg_color_change 10s infinite alternate; }

.footnote {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: yellow;
  padding: 1rem;
  box-shadow: 0px -2px 10px rgba(0, 0, 0, 0.5); }
