.header {
  text-align: center; }
  .header h1 {
    letter-spacing: 0; }
    @media screen and (min-width: 768px) {
      .header h1 {
        font-size: 3rem; } }
  .header__links {
    margin-top: 0.75rem;
    display: flex;
    justify-content: space-evenly; }
    @media screen and (min-width: 768px) {
      .header__links {
        justify-content: unset; } }
    @media screen and (min-width: 768px) {
      .header__links > * {
        margin-right: 1rem; } }
  @media screen and (min-width: 768px) {
    .header {
      text-align: left; } }
