.section-seo {
  background-color: #4fc0ec; }

.trifecta {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center; }
  .trifecta svg {
    height: 4rem;
    width: 4rem; }
  .trifecta__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1rem 0; }
    .trifecta__item.top {
      width: 100%;
      padding: 1rem 5rem; }
    .trifecta__item.left {
      width: 50%; }
    .trifecta__item.right {
      width: 50%; }
    .trifecta__item p {
      max-width: 50%;
      text-align: center; }
